<template>
    <div class="head">
        <div class="head_box">
            <div class="breadcrumb">
                <div class="breadcrumb_left">
                    <slot></slot>
                </div> 
                <div class="breadcrumb_right">
                    <el-breadcrumb separator="/">
                        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item v-for="(item,index) in matched" :key="index">{{ item.meta.title }}</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
            </div>
            
            <div class="userInfo">
                <span v-if="userInfo.data[0].admin_jurisdiction == 'super_editor'">超级管理员</span>
                <span v-if="userInfo.data[0].admin_jurisdiction == 'admin'">管理员</span>
                <span v-if="userInfo.data[0].admin_jurisdiction == 'staff'">访问者</span>
                <img src="https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif?imageView2/1/w/80/h/80" alt="">
                <el-dropdown>
                    <span class="el-dropdown-link">
                        <i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="updatePsw">修改密码</el-dropdown-item>
                        <el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>    
    </div>
</template>

<script>
export default {
    props:{
        isCollapse: Boolean
    },
    data(){
        return {
            roles:"",
            userInfo:"",
            matched:"",
        }
    },
    created() {
        console.log(this.isCollapse);
        console.log(this.$store.state.userInfo)    
        this.userInfo = this.$store.state.userInfo;
        this.getRouterTitle();
    },
    methods: {
        logout () {
            sessionStorage.clear();
            this.$router.push({ path:'/login' });
        },
        getRouterTitle () {
            let matched = this.$route.matched.filter(item => item.meta.title);
            this.matched = matched;
        },
        updatePsw () {

        }
    },
    watch: {
        $route(to, from) {
            this.getRouterTitle()
        }
    }
}
</script>
<style lang="scss" scoped>
    .head {
        height: 50px;
        overflow: hidden;
        position: relative;
        background: #fff;
        -webkit-box-shadow: 0 1px 4px rgba(0,21,41,.08);
        box-shadow: 0 1px 4px rgba(0,21,41,.08);
    }
    .head_box {
        display: flex;
        padding: 0 20px;
        .breadcrumb {
            display: flex;
            flex: 1;
            align-items: center;
            .breadcrumb_left {
                margin-right: 20px;
            }
        }
        .userInfo {
            display: flex;
            align-items: center;
            justify-content: center;
            span {
                font-size: 14px;
                margin-right: 10px;
            }
            img {
                width: 30px;
                height: 30px;
                border-radius: 10px;
            }
            .el-dropdown {
                margin-top: 15px;
            }
        }
    }
    .el-breadcrumb {
        line-height: 50px;
    }
    .el-breadcrumb__separator {
        color: #333;
    }
</style>


