<template>
    <div class="home" style="height: 100%;">
        <div class="left">
            <el-menu
                router
                background-color="#324057"
                text-color="#fff"
                default-active="1-4-1" 
                class="el-menu-vertical-demo" 
                @open="handleOpen"
                @close="handleClose"
                :collapse-transition="false"
                :collapse="isCollapse" style="min-height: 100%;">
                <el-menu-item index="home">
                    <i class="el-icon-s-data"></i>
                    <span slot="title">首页</span>     
                </el-menu-item>
                <el-menu-item index="user">
                    <i class="el-icon-user"></i>
                    <span slot="title">用户列表</span>
                </el-menu-item>
                <el-menu-item index="order">
                    <i class="el-icon-s-order"></i>
                    <span slot="title">订单列表</span>
                </el-menu-item>
                <el-menu-item index="banner">
                    <i class="el-icon-menu"></i>
                    <span slot="title">广告列表</span>
                </el-menu-item>
                
                <el-submenu index="1">
                    <template slot="title">
                        <i class="el-icon-goods"></i>
                        <span slot="title">权限管理</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="adminUser">
                            <span slot="title">用户管理</span>
                        </el-menu-item>
                        <el-menu-item index="addAdminUser">
                            <span slot="title">增加用户</span>
                        </el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
            </el-menu>    
        </div>
        <div class="right">
            <Head>
                <div class="mentBtn" @click="bindMentBtn">
                    <i :class="isIcon"></i>
                </div>
            </Head>
            <div class="content_box">  
                <router-view></router-view>
            </div>
        </div>  
    </div>
</template>

<script>
    import Head from '../../components/head'
    export default {
        data() {
            return {
                isCollapse: true,
                isIcon:"el-icon-s-unfold"
            };
        },
        methods: {
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            },
            bindMentBtn () {
                if ( this.isCollapse ) {
                    this.isCollapse = false;
                    this.isIcon = 'el-icon-s-fold';
                } else {
                    this.isCollapse = true;
                    
                    this.isIcon = 'el-icon-s-unfold';
                }
                
            }
        },
        mounted() {
        
        },
        components:{
            Head
        }
    }
</script>
<style lang="scss" scoped>
    .home {
        display: flex;
        .right {
            flex: 1;
        }
        .mentBtn i {
            font-size: 26px;
        }
    }
    .content_box {
        padding: 20px;
        background-color: #f0f2f5;
        position: relative;
        width: 100%;
        height: 100%;
    }
    .el-menu--dark .el-submenu .el-menu {
        background-color: #1f2d3d;
    }
    
</style>


